import React,{useState,useContext} from 'react';
import {useQuery} from 'react-query';
import {A} from '../AppProvider'
const fetchTodoList= async({ queryKey })=>{
  //  https://api.coingecko.com/api/v3/simple/price?ids=bitcoin&vs_currencies=USD
  // https://jsonplaceholder.typicode.com/todos/1
   console.log("fetchTodoList has been called!");
  const a = await fetch(`https://api.coingecko.com/api/v3/simple/price?ids=bitcoin&vs_currencies=USD`);
  const b =await a.json();
  return b;

}

export default function Basic01() {
    const [coin,setCoin]=useState(false);
    const [value,setValue]=useContext(A);
    const { isLoading, isError, data, error } = useQuery(['todos'], fetchTodoList,
    {
        staleTime:6000,
        cacheTime:1000*15,
        // refetchInterval:1000*10,
    });

    // console.log(data);
    // console.log(coin);
    if (isLoading) {
      return <span>Loading, and btw, the state from AppProvider is ${JSON.stringify(value)}...</span>
    }
  
    if (isError) {
      return <span>Error: {error.message}</span>
    }
  
    // We can assume by this point that `isSuccess === true`
    return (<>
    <nav className="navbar navbar-expand-lg navbar-light bg-light">
  <div className="container-fluid">
    <a className="navbar-brand" href="#">Navbar</a>
    <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarNav" aria-controls="navbarNav" aria-expanded="false" aria-label="Toggle navigation">
      <span className="navbar-toggler-icon"></span>
    </button>
    <div className="collapse navbar-collapse" id="navbarNav">
      <ul className="navbar-nav">
        <li className="nav-item">
          <a className="nav-link active" aria-current="page" href="#">Home</a>
        </li>
        <li className="nav-item">
          <a className="nav-link" href="#">Features</a>
        </li>
        <li className="nav-item">
          <a className="nav-link" href="#">Pricing</a>
        </li>
        <li className="nav-item">
          <a className="nav-link disabled" href="#" tabIndex="-1" aria-disabled="true">Disabled</a>
        </li>
      </ul>
    </div>
  </div>
</nav>
    <button onClick={()=>{setCoin(!coin)}}>Click me to alter the state!</button>
      <ul>
          
<li>{JSON.stringify(data)}</li>
    <li>{coin.toString()}</li>
      </ul>
    </>)
}