        console.log(JSON.stringify(doc.data()));
        var tbl = doc.data().tbl;
        drawTheBinsTable(props, tbl);

        var source = doc.metadata.hasPendingWrites ? "Local" : "Server";
        console.log(source, " data: ", doc.data());

        // regular notification
