import React from 'react'
import Navbar from './Navbar'
import Kaggen from "./Kaggen"
import Seedloaf from './Seedloaf'
import White from './White';
import 'animate.css'
import SvgComponent from './SvgComponent'
import RecipeGenerator from './RecipeGenerator';

import {
    BrowserRouter as Router,
    Switch,
    Route,
    Link
  } from "react-router-dom";

export default function GlobalRouter(props) {

    return (
<Router>
    <div>
      {/* ideally we want the navbar to hold all the recipes. Color them green if they're active? */}
      {props.loginstate?<Navbar></Navbar>:<h1 style={{textAlign:'center'}}>Please log in</h1> }
      

    </div>
    
    
    <Switch>
          <Route path="/kaggenbrot">
            <Kaggen db={props.db} />
          </Route>
          <Route path="/seedloaf">
            <Seedloaf db={props.db} />
          </Route>
          <Route path="/white">
            <White/>
          </Route>
          <Route path="/">
            {/* <h1>This is only meant to run when at /....</h1> */}
          {props.loginstate ? <button onClick={() => { props.signOut(props.setloginstate) }}>LOG OUT</button> : null}
          {props.loginstate ? <RecipeGenerator db={props.db} recipe={true}></RecipeGenerator> : <SvgComponent></SvgComponent>}
          </Route>

        </Switch>
    
    
    

    </Router>

    )
}
