import React from 'react'
import Tabulator from 'tabulator-tables';
import sound from '../sounds/1.wav'
import { useEffect, useState } from 'react'
import {useQuery} from 'react-query';

export default function Kaggen(props) {

    // Data for the table
    var tbl = [{ ingredient: "Rye Flour", culture1: 23, culture2: 18, culture3: 5, culture4: 0, levain1: 193, levain2: 521, levain3: 0, soaker1: 0, soaker2: 0, final: 502 },
    { ingredient: "Rye White Meal", culture1: 0, culture2: 0, culture3: 0, culture4: 0, levain1: 0, levain2: 0, levain3: 0, soaker1: 0, soaker2: 0, final: 699 },
    { ingredient: "Rye Crushed", culture1: 0, culture2: 0, culture3: 0, culture4: 0, levain1: 0, levain2: 0, levain3: 0, soaker1: 386, soaker2: 0, final: 0 },
    { ingredient: "Water", culture1: 23, culture2: 18, culture3: 5, culture4: 0, levain1: 125, levain2: 762, levain3: 0, soaker1: 648, soaker2: 406, final: 178 },
    { ingredient: "Salt", culture1: 0, culture2: 0, culture3: 0, culture4: 0, levain1: 0, levain2: 0, levain3: 0, soaker1: 8, soaker2: 0, final: 35 }];
    const [multiplier, setMultiplier] = useState(1);

    // firebaseData stores the data from firebase. setFirebaseData sets the data gotten from firebase
    const [firebaseData, setfirebaseData] = useState([]);

    const handleSubmit = (evt) => {
        
        evt.preventDefault();
        props.db.collection("rec").doc("multiplier").set({ multiplier: multiplier }).then(function () {
            console.log("Document successfully written for the multiplier!");
        })
            .catch(function (error) {
                console.error("Error writing document: ", error);
            });
        alert(`Submitting Name ${multiplier}`)
    }
    function drawThePricesTable() {
        var s = new Tabulator("#kaggenbrot", {
    
            data: tbl,
    
            cellEdited: function (cell) {
    
            },
            layout: "fitColumns",
            columns: [{ title: "Ingredient", headerFilter: "input", field: "ingredient", hozAlign: "center", editor: true },
    
            {
                title: "Culture1", field: "culture1", hozAlign: "center", cellClick: function (e, cell) {
                    console.log(e.target.style.color = 'red');
                    console.log(cell);
                }
            }, {
                title: "Culture2", cellClick: function (e, cell) {
                    console.log(e.target.style.color = 'red');
                    console.log(cell);
                }, field: "culture2", hozAlign: "center",
            },
            {
                title: "Culture3", cellClick: function (e, cell) {
                    console.log(e.target.style.color = 'red');
                    console.log(cell);
                }, field: "culture3", hozAlign: "center",
            },
            {
                title: "Culture4", cellClick: function (e, cell) {
                    console.log(e.target.style.color = 'red');
                    console.log(cell);
                }, field: "culture4", hozAlign: "center",
            }, {
                title: "Levain1", cellClick: function (e, cell) {
                    console.log(e.target.style.color = 'red');
                    console.log(cell);
                }, field: "levain1", hozAlign: "center",
            }, {
                title: "Leavain2", cellClick: function (e, cell) {
                    console.log(e.target.style.color = 'red');
                    console.log(cell);
                }, field: "levain2", hozAlign: "center",
            },
            {
                title: "Leavain3", cellClick: function (e, cell) {
                    console.log(e.target.style.color = 'red');
                    console.log(cell);
                }, field: "levain3", hozAlign: "center",
            },
            {
                title: "Soaker1", cellClick: function (e, cell) {
                    console.log(e.target.style.color = 'red');
                    console.log(cell);
                }, field: "soaker1", hozAlign: "center",
            },
            {
                title: "Soaker2", cellClick: function (e, cell) {
                    console.log(e.target.style.background = 'red');
                    console.log(cell);
                }, field: "soaker2", hozAlign: "center",
            },
            {
                title: "Final", cellClick: function (e, cell) {
                    console.log(e.target.style.color = 'red');
                    console.log(cell);
                }, field: "final", hozAlign: "center",
            },
    
    
            ]
    
    
        });
    
    }
    function updateDates(paa){
        console.log(paa.date);
        paa.date=new Date(Date.now()+1000*60*120);
    
        updateFirebaseWithNewData(props,firebaseData,multiplier);
    
    }
    function playAudio(){
            var audio = document.getElementById("poppy");
            audio.volume=0.5;
            audio.play();
    }
    function updateFirebaseWithNewData() {

        console.log('updateFirebaseWithNewData');
        props.db.collection("rec").doc("kaggenbrot").set({ kaggenbrot: firebaseData }).then(function () {
            console.log("Document successfully written for the recipe instructions!");
        })
            .catch(function (error) {
                console.error("Error writing document: ", error);
            });
    
    }
    useEffect(() => {
        
        drawThePricesTable();
        return () => {
            // cleanup
        }
    }, [multiplier]);

    useEffect(() => {
                props.db.collection("rec").doc("kaggenbrot")
            // onSnapshot subscribes to any changes in the doc. It uses websockets behind the scenes to get live data.
            .onSnapshot(function (doc) {
                // console.log(JSON.stringify(doc.data()));
                setfirebaseData(doc.data().kaggenbrot)

            });
            props.db.collection("rec").doc("multiplier")
            // onSnapshot subscribes to any changes in the doc. It uses websockets behind the scenes to get live data.
            .onSnapshot(function (doc) {
                // console.log(JSON.stringify(doc.data()));
                // setfirebaseData(doc.data().kaggenbrot)
                setMultiplier(doc.data().multiplier)

            });
        return () => {
          
        }
    }, [])
    // const kaggenbrotRecipe = [{ title: 'Day 0 0800', activeBake: false, steps: [{ directions: 'Culture #1,2,3', date: "" }, { directions: "Prepare 100g Rye Culture (100% hydration) from mother culture", date: "" }] }, { title: 'Day 1 0600', steps: [{ directions: 'Levain 1 (the bomb),', date: "" }, { directions: "Basic Sour (70%-75% hydration)", date: "" }, { directions: "Using Culture#1, mix water and flour into a round ball, ie 'the bomb'", date: "" }, { directions: "Allow to mature for 15 to 24 hours at 25 degrees celcius", date: "" }] }];



    tbl.forEach((x) => {
        for (var p in x) {
            if (typeof (x[p]) == 'number') {
                x[p] = multiplier * x[p]

            }
        }
    });
    console.log(firebaseData);
    const mapped = firebaseData.map((x)=>{
        return x.steps.map((y,i)=>{
            // each step with timestamp;
            var insertDate=""
            if(y.date==""){
                insertDate=""
            }
            else{
                insertDate="Completed at" + ((new Date(parseInt(y.date.seconds*1000)).toLocaleString('en-GB')))
            }
            return (
                <div style={{border:'1px solid black'}} key={i} >
                    {/* <div  onClick={()=>{updateDates(y);}}class="form-check">
                        <input class="form-check-input" type="checkbox" value="" id="flexCheckIndeterminate"/>
                        <label class="form-check-label" htmlFor="flexCheckIndeterminate">
                        checkbox
                        </label>
                    </div> */}
                    <div  onClick={(e)=>{updateDates(y);playAudio()}} class="form-check form-switch">
                            <input class="form-check-input" type="checkbox" id="flexSwitchCheckDefault" checked={y.date==""?false:true}/>
                            <label class="form-check-label" htmlFor="flexSwitchCheckDefault"></label>
                    </div>
                    <div style={{display:"inline-block",width:'50vw',}}>{JSON.stringify(y.directions)}
                    </div>
                    <div style={{display:"inline-block",background:'green',fontSize:"2em"}}>{insertDate}
                    </div>
                    <br></br>
                </div>)
        });

    });

function clearDates(){
    firebaseData.forEach((x)=>{
        x.steps.forEach((y)=>{
            y.date=""
        })
    });
    updateFirebaseWithNewData(props,firebaseData,multiplier);
}
    return (
        <div onClick={(e) => { console.log('outer most clicked'); }} >
            <audio controls id='poppy' preload='auto' style={{display:"none"}}>
    <source src={sound} type="audio/ogg"/>
    <source src={sound} type="audio/mpeg"/>
    Your browser does not support the audio element.
        </audio>
            <form onSubmit={handleSubmit}>
                <label>
                    Multiplier:
        <input
                        type="text"
                        value={multiplier}
                        onChange={e => setMultiplier(e.target.value)}
                    />
                </label>
                <input type="submit" value="Submit" />
            </form>
            <label>No. Loaves: </label><span> {multiplier * 6}</span>

            <div id="kaggenbrot"></div>
            <button onClick={clearDates}>Clear</button>
            {mapped}
            
        </div>
    )
}
