import { React, createContext, useContext, memo, useState } from 'react';
import { useQuery, QueryClient, QueryClientProvider } from 'react-query';
import { ReactQueryDevtools } from 'react-query/devtools';
import Basic01 from './Sub-Component/Basic01'

// createContext is used for global state. Every component definied within WhiteContent will have access to the state 

const WhiteContext = createContext();
const queryClient = new QueryClient();

export default function White() {
    const [buttonState, setButtonState] = useState(false);
    // WhiteContext.Provider by definition is a function, 
    return (<WhiteContext.Provider value={{ buttonState, setButtonState }}>
            <p>
  <a className="btn btn-primary" data-bs-toggle="collapse" href="#collapseExample" role="button" aria-expanded="false" aria-controls="collapseExample">
    Link with href
  </a>
  <button className="btn btn-primary" type="button" data-bs-toggle="collapse" data-bs-target="#collapseExample" aria-expanded="false" aria-controls="collapseExample">
    Button with data-bs-target
  </button>
</p>
<div className="collapse" id="collapseExample">
  <div className="card card-body">
    Anim pariatur cliche reprehenderit, enim eiusmod high life accusamus terry richardson ad squid. Nihil anim keffiyeh helvetica, craft beer labore wes anderson cred nesciunt sapiente ea proident.
  </div>
</div>
                <button onClick={()=>{setButtonState(!buttonState)}}>CLCK ME!</button>
                <div >
                {buttonState ? <><Basic01></Basic01><Basic01></Basic01></>: null}
                {/* <Basic01></Basic01> */}
                {/* <Basic01></Basic01> */}
                {/* <Basic01></Basic01> */}
                {/* <Basic01></Basic01> */}
                </div>


    </WhiteContext.Provider>
    )
}






